body {
  margin: 0;
}

.stop-watch {
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stop-watch__center {
  background-color: #f90;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stop-watch__min {
  z-index: -1;
  transform-origin: bottom;
  background-color: #00f;
  width: 2vmin;
  height: 20vmin;
  margin: auto;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 2.5vmin;
  left: 0;
  right: 0;
}

.stop-watch__second {
  z-index: -2;
  transform-origin: bottom;
  background-color: green;
  width: 1vmin;
  height: 40vmin;
  margin: auto;
  animation: 5s linear infinite move;
  position: absolute;
  bottom: 2.5vmin;
  left: 0;
  right: 0;
}

@keyframes move {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

